import React from 'react';

//  [  ] : npm i react-router-dom == npm install react-router-dom
// import {  } from 'react-router-dom'; v6
import { HashRouter, BrowserRouter, Routes, Route, Link,  Router } from "react-router-dom";
import styles from "./css/Index.css";
import SecurityCard from "./security/SecurityCard"; //  
import SecurityCardPassword from "./security/SecurityCardPassword"; //    
import Bank from './Bank/Account'; //  
import DealReport from "./Bank/DealReport"; //  
import DealReportPassword from "./Bank/DealReportPassword"; //   
    // BrowserRouter :  URL
    //  vs HashRouter :
    // Routes :  Route /
    // Route :   URL  -->  URL     [ SPA ]
    // Link :   <---> a   : 
        // Link to = "Route Path"
    // Router :
/* 2022-12-06    */
/* 2022-12-09      */
/* 2022-12-11      */
export default function Index( props ){
    return  (
        <div className="webbox">
            <BrowserRouter>
                    <ul>
                        <li><a href="/">URL</a></li>
                        <li><a href="/bank/securityCard/password"></a></li>
                        <li><a href="/bank/account"></a></li>
                        <li><a href="/bank/dealReport/password">  </a></li>
                    </ul>
                    <Routes>
                        <Route path="/bank/securityCard/password" element={ <SecurityCardPassword/> }/>
                        <Route path="/bank/securityCard" element={ <SecurityCard/> }/>
                        <Route path="/bank/account" element={ <Bank/> }/>
                        <Route path="/bank/dealReport/password" element={ <DealReportPassword/> }/>
                        <Route path="/bank/dealReport" element={ <DealReport/> }/>
                    </Routes>
            </BrowserRouter>
        </div>
    );
}