import React, { useState, useRef } from 'react';
import '../css/SecurityCardPassword.css';
import { useNavigate } from 'react-router-dom'; //  //  : 6 useNavigate .
import axios from 'axios';


/* 2022-12-07  4    password */
/* 2022-12-08     */
export default function SecurityCardPassword(){
    // react location.href    useHistory  .
    const navigate = useNavigate();
    function handleUseHistory() {
        navigate('/bank/securityCard'); //"/member/securityCard" url 
     }
    // dom    useRef .
    // dom  password  input   .
    const password = useRef(); // useRef()   .
    function SubmitButton (){
    console.log(password.current.value);
    alert(password.current.value);
    // passwordData  axios    .
    let passwordData = {acpw : password.current.value};
    axios.post("http://localhost:8080/bank/securityCard/Password", passwordData)
    .then((re)=>{
        console.log("SubmitButtong ??");
        console.log("re: " + re.data);
        if(re.data ===1){
           handleUseHistory();
        }else{ alert("   .")}
        }
    )
    .catch((error)=>{ console.log(error);})
    }

    return(
    <div className="passwordComponent">
        <span className="passwordContent">  4 .</span><br/>
        <input type="password" maxLength="4" className="passwordInput" ref={password} placeholder="****"/>
        <button type="button" className="submitButton" onClick={SubmitButton}></button>
    </div>
    );
}