import React from 'react';
import '../css/DealReport.css'



export default function DealReport (){
    return(
        <div className="dealReport">
            <div className="dealReportComtent"> </div>
            <table className="dealReportTable">
                <tr className="dealReportTrComponent">
                    <th className="orderComponent"></th>
                    <th className="categoryComponent"></th>
                    <th className="contentComponent"></th>
                    <th className="moneyComponent"></th>
                </tr>
                <tr className="dealReportTrComponent">
                    <th className="orderComponentList"></th>
                    <th className="categoryComponentList"></th>
                    <th className="contentComponentList"></th>
                    <th className="moneyComponentList"></th>
                </tr>
            </table>
         </div>
    );
}